import React from 'react';
import { StyleSheet, css } from 'aphrodite';
import { User } from '../../../api/github';

interface ProfileProps {
    data: User | null;
}

export default function Profile(props: ProfileProps) {
    const data = props.data;

    if (data == null) return null;

    return (
        <div className='row'>
            <div className='col-12 col-md-4'>
                <img
                    src={data.avatar_url}
                    alt=''
                    className={`${css(styles.profileImage)} img-fluid`}
                />
            </div>
            <div className='col-12 mt-2 col-md-8'>
                <h2>{data.name}</h2>
                <a href={data.html_url} className='h6'>
                    @{data.login}
                </a>
                <h6>{data.company ?? 'No company'}</h6>
                <h6>{data.location ?? 'Unknown location'}</h6>
                <a href={data.html_url}>{data.html_url}</a>
                <div className='d-flex justify-content-around justify-content-lg-start mt-3'>
                    <div className='text-center me-lg-3'>
                        <h2>{data.followers}</h2>
                        <h6>Followers</h6>
                    </div>
                    <div className='text-center mx-lg-3'>
                        <h2>{data.following}</h2>
                        <h6>Following</h6>
                    </div>
                    <div className='text-center mx-lg-3'>
                        <h2>{data.public_repos}</h2>
                        <h6>Repositories</h6>
                    </div>
                </div>
            </div>
        </div>
    );
}

const styles = StyleSheet.create({
    profileImage: {
        background: '#c5c5c5'
    }
});
