import React from 'react';
import { StyleSheet, css } from 'aphrodite';
import { ellipsis } from '../../../utils/text';
import { Link } from 'react-router-dom';

interface SearchResultsItemProps {
    data: {
        avatar_url: string;
        login: string;
    };
}

export default function SearchResultsItem(props: SearchResultsItemProps) {
    const { avatar_url, login } = props.data;

    return (
        <Link to={`/user/${login}`} className={css(styles.link)}>
            <div
                className={`${css(
                    styles.container
                )} col d-flex align-items-center justify-content-center py-2 px-3 my-0 my-lg-3`}>
                <img src={avatar_url} alt='' className={css(styles.image)} />
                <h6 className={css(styles.name)}>{ellipsis(login)}</h6>
            </div>
        </Link>
    );
}

const styles = StyleSheet.create({
    container: {
        height: 70,
        ':hover': {
            backgroundColor: 'rgb(230, 230, 230)',
            borderRadius: 5
        }
    },
    image: {
        width: 50,
        height: 50,
        marginRight: '10px',
        borderRadius: '100%',
        backgroundColor: 'grey'
    },
    name: {
        flex: 1
    },
    link: {
        textDecoration: 'none',
        color: 'black'
    }
});
