const github = 'https://api.github.com';

export interface SearchUsersProps {
    username: string;
    perPage?: number;
    page?: number;
}

export interface SearchUsersResult {
    total_count: number;
    incomplete_results: boolean;
    items: Array<{
        avatar_url: string;
        login: string;
    }>;
}

/**
 *
 * Search all the matched users with a username
 */
function searchUsers({
    username,
    perPage = 15,
    page = 1
}: SearchUsersProps): Promise<SearchUsersResult> {
    const url = `${github}/search/users?q=${username}&per_page=${perPage}&page=${page}`;
    return fetch(url).then((res) => res.json());
}

export interface User {
    avatar_url: string;
    company: string | null;
    html_url: string;
    location: string | null;
    public_repos: number;
    followers: number;
    following: number;
    name: string;
    login: string;
    updated_at: Date;
    bio: string;
}

/**
 *
 * Get all the data of a single user
 */
function getUser(username: string): Promise<User> {
    const url = github + '/users/' + username;
    return fetch(url).then((res) => res.json());
}

export interface Repository {
    name: string;
    full_name: string;
    forks_count: number;
    language: string;
    created_at: string;
    pushed_at: string;
    updated_at: string;
    description: string;
    html_url: string;
    fork: boolean;
    visibility: string;
}

/**
 *
 * Get all the repositories of a single user
 */
function getRepositories(username: string): Promise<Repository[]> {
    const url = `${github}/users/${username}/repos`;
    return fetch(url).then((res) => res.json());
}

export { searchUsers, getUser, getRepositories };
