import React from 'react';
import Item from './Item';
import { css, StyleSheet } from 'aphrodite';
import { SearchUsersResult } from '../../api/github';
import { PaginationData } from '../../App';

interface SearchResultsProps {
    data: SearchUsersResult | undefined;
    pagination: {
        data: PaginationData;
        next: () => void;
        previous: () => void;
    };
}

export default function SearchResults(props: SearchResultsProps) {
    const { data } = props;
    const { hasNextPage, hasPreviousPage } = props.pagination.data;
    const { next, previous } = props.pagination;

    return (
        <div className={`container-fluid my-3 h-100 d-flex flex-column`}>
            <div className={css(styles.resultsContainer)}>
                <div className='row row-cols-1 row-cols-md-2 row-cols-lg-4 mx-2 mx-md-0'>
                    {data?.items.map((user) => (
                        <Item data={user} key={user.login} />
                    ))}
                </div>
            </div>
            <div className='row mb-5'>
                <div className='col d-flex justify-content-between'>
                    <div>
                        {hasPreviousPage && (
                            <button
                                className='btn btn-outline-dark'
                                onClick={() => previous()}>
                                {`<`} Previous
                            </button>
                        )}
                    </div>
                    <div>
                        {hasNextPage && (
                            <button
                                className='btn btn-outline-dark'
                                onClick={() => next()}>
                                Next {`>`}
                            </button>
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
}

const styles = StyleSheet.create({
    resultsContainer: {
        flex: 1
    }
});
