import React, { useState } from 'react';
import Footer from './components/Footer';
import Header from './components/Header';
import { StyleSheet, css } from 'aphrodite';
import SearchResults from './components/SearchResults';
import { Routes, Route, useNavigate } from 'react-router-dom';
import User from './components/User';
import { SearchUsersResult } from './api/github';
import { searchUsers } from './api/github';
import Loading from './components/Loading';
import Error from './components/Error';

export interface PaginationData {
    usedUsername: string;
    currentPage: number;
    totalPage: number;
    hasNextPage: boolean;
    hasPreviousPage: boolean;
}

const DATA_PER_PAGE = 15;

function App() {
    const [username, setUsername] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const [hasError, setHasError] = useState(false);
    const [searchResults, setSearchResults] = useState<
        SearchUsersResult | undefined
    >(undefined);
    const navigate = useNavigate();
    const [paginationData, setPaginationData] = useState<PaginationData>({
        usedUsername: '',
        currentPage: 1,
        totalPage: 1,
        hasNextPage: false,
        hasPreviousPage: false
    });

    function handleFormSubmit(e: React.FormEvent<HTMLFormElement>) {
        e.preventDefault();
        if (username.length > 2) {
            navigate('/');
            getUsers({ username });
        }
    }

    function next() {
        getUsers({ page: paginationData.currentPage + 1 });
    }

    function previous() {
        getUsers({ page: paginationData.currentPage - 1 });
    }

    function getUsers({ username = paginationData.usedUsername, page = 1 }) {
        setSearchResults(undefined);
        setIsLoading(true);
        setHasError(false);
        searchUsers({ username, page })
            .then((data) => {
                setPaginationData(() => {
                    const totalPage = Math.floor(
                        data.total_count / DATA_PER_PAGE
                    );
                    return {
                        usedUsername: username,
                        currentPage: page,
                        totalPage: totalPage,
                        hasNextPage: page < totalPage,
                        hasPreviousPage: page > 1
                    };
                });
                setSearchResults(data);
            })
            .catch(() => setHasError(true))
            .finally(() => setIsLoading(false));
    }

    function renderBody() {
        if (isLoading) return <Loading />;
        if (hasError) return <Error />;
        return (
            <Routes>
                <Route
                    path='/'
                    element={
                        <SearchResults
                            data={searchResults}
                            pagination={{
                                data: paginationData,
                                next,
                                previous
                            }}
                        />
                    }
                />
                <Route path='/user/:username' element={<User />} />
            </Routes>
        );
    }

    return (
        <div className={css(styles.container)}>
            <Header
                setUsername={setUsername}
                handleFormSubmit={handleFormSubmit}
            />
            <div className={css(styles.body)}>{renderBody()}</div>
            <Footer />
        </div>
    );
}

const styles = StyleSheet.create({
    container: {
        width: '100%',
        height: '100vh',
        display: 'flex',
        flexDirection: 'column'
    },
    body: {
        flex: 1
    }
});

export default App;
