import React from 'react';
import githubLogo from '../../assets/github.png';
import { StyleSheet, css } from 'aphrodite';

interface HeaderProps {
    setUsername: React.Dispatch<React.SetStateAction<string>>;
    handleFormSubmit: (e: React.FormEvent<HTMLFormElement>) => void;
}

export default function Header(props: HeaderProps) {
    const { setUsername, handleFormSubmit } = props;

    return (
        <header className='bg-light py-2 shadow-sm'>
            <div className='container'>
                <div className='row'>
                    <div className='col-12 mt-2 col-lg-7'>
                        <div className='d-flex align-items-center'>
                            <img
                                alt='github logo'
                                src={githubLogo}
                                className={css(styles.logo)}
                            />
                            <h2 className='m-0'>Github User Search</h2>
                        </div>
                        <h6>Browse users and their profile</h6>
                    </div>
                    <div className='col-12 mt-1 mb-3 col-lg-5 d-flex align-items-end'>
                        <form
                            className='d-flex w-100'
                            onSubmit={handleFormSubmit}>
                            <input
                                required
                                placeholder='Enter username'
                                minLength={2}
                                type='text'
                                className='form-control rounded-0'
                                onChange={(e) => setUsername(e.target.value)}
                            />
                            <button
                                type='submit'
                                className='btn btn-outline-dark rounded-0'>
                                Search
                            </button>
                        </form>
                    </div>
                </div>
            </div>
        </header>
    );
}

const styles = StyleSheet.create({
    logo: {
        width: 30,
        height: 30,
        marginRight: '10px'
    }
});
